/* animation car lorry */
.btn-bl {
  min-height: 50px;
  -webkit-animation-name: btn-moves; /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 1s; /* Safari 4.0 - 8.0 */
  animation-name: btn-moves;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  background-color: #3d7abb !important;
}
.btn-bl::after {
  -webkit-animation-name: btn-moves-after; /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 0.8s; /* Safari 4.0 - 8.0 */
  animation-name: btn-moves-after;
  animation-duration: 0.8s;
  animation-delay: 3s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  content: "";
}
.car-lorry {
  bottom: 30px;
  right: -60px;
  width: 60px;
  position: absolute;
  z-index: 10;
}
.ani-form .car-move {
  -webkit-animation-name: car-moves; /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 1.5s; /* Safari 4.0 - 8.0 */
  animation-name: car-moves;
  animation-duration: 3s;
  animation-delay: 1.2s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

/* Standard syntax */
@keyframes car-moves {
  0% {
    right: -46px;
  }
  50% {
    right: 45%;
  }
  70% {
    right: -60px;
  }
  100% {
    right: -60px;
  }
}

/* Standard btn */
@keyframes btn-moves {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(0.85);
    margin: auto;
    background-color: #3d7abb !important;
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes btn-moves-after {
  0% {
    content: "";
  }
  100% {
    content: "Checked out";
  }
}
@keyframes btn-moves-after {
  0% {
    content: "";
  }

  100% {
    content: "Checked out";
  }
}

